"use client";
import { Header } from "@/components/navigation/header";
import { Sidebar } from "@/components/navigation/sidebar";
import { TagBar } from "@/components/navigation/tag-bar";
import { ENDPOINTS } from "@/shared/site";
import { getRequest } from "@/utils/axios.server";
import axios from "axios";
import { cookies } from "next/headers";
import { useEffect } from "react";

async function getData() {
  const cookieHeader = cookies().toString();

  const url = `${ENDPOINTS.http_server_base}${ENDPOINTS.transaction_list}`;

  const response = await fetch(url, {
    method: "GET",
    headers: {
      Cookie: cookieHeader,
    },
  });

  if (!response.ok) {
    throw new Error(`Failed to fetch ${url}: ${response.status}`);
  }

  const data = await response.json();
  return data;
}

export default function User() {
  //   const data = await getData();
  const get_data = async () => {
    try {
      const { data } = await getRequest({ url: ENDPOINTS.transaction_list });
    } catch {}
  };
  useEffect(() => {}, []);
  return (
    <main>
      <TagBar />
      <div className="flex">
        <Sidebar />
        <div className="flex-1 min-h-screen pt-6">
          <Header />
          <div className="h-screen p-4">{/* <CustomTable /> */}</div>
        </div>
      </div>
    </main>
  );
}
